
$third: #868686;
$border: #74C991;

.sidebar {
    display: flex;
    flex-direction: column;
    justify-content: center;
    position: fixed;
    padding: 10px;
    top: 0;
    left: 0px;
    gap: 10px;
    width: 50px;
    top: 50%;
    outline: 0 solid $border;
    border-radius: 0 6px 6px 0;
    transition: 300ms 200ms;
    z-index: 10;
}

.sidebar-hover {
    transition: 300ms;
    width: 120px;
    background-color: #1f1f1ff1;
    outline: 2px solid $border;
}

.social-div {
    display: flex;
    gap: 5px;
    align-items: center;
}

.social-icon {
    filter:drop-shadow(2px 2px 2px rgba(0, 0, 0, 0.874)) ;
}

.social-div:hover, .social-icon:hover {
    color: $third;
}

@media screen and (max-width: 1099px) {
    .sidebar {
        display: none;
    }
}