
$border: #74C991;

.App {
    position: relative;
    
}

.top-button {
    z-index: 6;
    font-size: 30px !important;
    padding: 10px;
    position: fixed;
    bottom: 15px;
    right: 15px;
    background-color: $border;
    border-radius: 100px;
    outline: 3px solid transparent;
    outline-offset: 6px;
visibility: none;
    animation: top-btn-rvl 0.2s linear forwards;
    cursor: pointer;
}

.top-button:hover {
    outline: 3px solid $border;
    outline-offset: -3px;
    background-color: #74c990a4;
    transition: 0.2s linear;
}

@keyframes top-btn-rvl {
    from {
opacity: 0;
transform: translateY(-20px);

    }

    to {
opacity: 1;
visibility: visible;
transform: translateY(0);
    }
}