
$shadow: rgba(0, 0, 0, 0.111);

$border: #74C991;

.languages {
    display: grid;
    gap: clamp(5px, 1vw, 10px);
    margin-block: 20px;
    grid-template-columns: 1fr 1fr 1fr 1fr 1fr;
    padding: 0;
   width: 100%;
}

.languages > li {
   
    height: clamp(30px, 14vw, 170px);
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    text-align: center;
    border: 1px $border solid;
    border-radius: 10px;
    box-shadow: 2px 2px 5px $shadow;
    position: relative;
    gap: 10px
}

.lang-img {
    user-select: none;
    -webkit-user-select: none;
    width: clamp(10px, 7vw, 90px);
    filter: saturate(0%) brightness(110%) drop-shadow(3px 3px 2px rgba(0, 0, 0, 0.445));
    transition: 0.3s;
}


#photoshop {
    width: 100px;
}

.modal {
visibility: hidden;
position: relative;
font-size: 0;
opacity: 0;
transition: 0.3s;
}

.modal-reveal {
    visibility: visible;
    opacity: 1;
    font-size: clamp(8px,2vw,20px)
}

.img-shrink {
    scale: 0.8;
    filter: saturate(100%) brightness(100%) drop-shadow(3px 3px 2px rgba(0, 0, 0, 0.445));
}