$first: #1E1E1D;
$border: #74C991;

main {
    margin: 0 auto 0 auto;
   background-color: $first;
   overflow: hidden;
}

.main-div {
padding: 80px 30px 30px 30px;
background-color: $first;
position: relative;
border: 1px solid #74C991;
max-width: 920px; 
box-shadow: 4px 4px 20px rgba(0, 0, 0, 0.621);
border-radius: 12px;
margin: 0 auto;

}

    @media screen and (max-width: 1099px) {
        .main-div {
            border: none;
            border-top: 1px solid $border;
            border-radius: 12px 12px 0 0;
            box-shadow: none;
        }

       
    }