$first: #1E1E1D;
$border: #74C991;

footer {
    margin: 0 auto;
    
    display: flex;
    flex-direction: column;
    align-items: center;
   

    position: relative;
}

.social {
    text-align: center;

width: 100%;

padding-block: 20px 15px;
}

.disclaimer {
   max-width: 920px;
margin: 20px 20px 0 20px;


   p {
    font-size: 11px;
    text-align: center;
    border: 1px solid rgba(255, 255, 255, 0.234);
    padding: 20px;
    
   }
}


.social-links {
    display: flex;
    
    width: 100%;
    justify-content: center;
    
    gap: 10px;
    
}

.foot-msg {
    background-color: $first;
    border-top: 1px solid #74C991;
    width: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    padding-block: 20px;
    position: relative;
   gap:5px
}

.foot-msg > p {
    font-size: clamp(0.7rem, 0.3268rem + 1.1707vw, 1rem);
}

.tools-div {
    display: flex;
  
   
    gap:8px;
   border-radius: 4px ;

}

.foot-tools {
    width: clamp(1rem, 0.3268rem + 1.1707vw, 1.3rem);
    filter: brightness(130%);
    
    margin-bottom: -5px;

}