$first: #1E1E1D;
$border: #74C991;

header {
    margin-bottom: 20px;
    text-transform: uppercase;
    position: fixed;
    top: 0;
    width: 100%;
    z-index: 100;
    transition: 300ms;
}

.above {
    background-color: rgb(31, 31, 31);
    box-shadow: rgba(0, 0, 0, 0.784) 5px 0px 10px;
    
}

.below {
   background-color: rgba(31, 31, 31, 0);
   box-shadow: 5px 0 10px rgba(0, 0, 0, 0);
}

.menu {
    display: flex;
    gap: 20px;
    font-size: 1rem;
    font-weight: 700;
    position: relative;
    visibility: visible;
}

.mobile {
    display: flex;
    gap: 20px;
    font-size: 1rem;
    font-weight: 700;
    position: relative;
    padding-left: 0;
    position: absolute;
    height: 100vh;
    width: 100vw;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    top: 0;
    left:0;
    z-index: 100;
    background-color: $first;
    font-size: 2em;
    visibility: hidden;
}

.revealed {
    visibility: visible;
    animation: menu-opacity 0.2s linear forwards;
}

@keyframes menu-opacity {
    from {
opacity: 0;
    }

    to {
opacity: 1;
    }
}

.unrevealed {
   
    animation: menu-hidden 0.2s linear forwards;
}

@keyframes menu-hidden {
    from {
        opacity: 1;
        visibility: visible;
    }

    to {
        opacity: 0;
        visibility: hidden;
    }
}

.menu > li, .mobile > li {
    border-bottom: 2px solid rgba(0, 0, 0, 0);
    cursor: pointer;
}

.menu > li:hover, .mobile > li:hover {
    transition: border-bottom 300ms;
    border-bottom:solid $border 2px;
}


.profile-div {
    border: 2px $border solid;
    border-radius: 100px;
    width: 50px;
    height: 50px;
   position: relative;
   background-color: rgba(255, 255, 255, 0.122);
    box-shadow: inset 0 0 20px $first;
}

.profile-div:hover {
animation: profile-hover 0.5s infinite ease-in;
}

@keyframes profile-hover {
    0% {
        transform: translateY(0px);
    }

    25% {
        transform: translateY(2px);
    }

    50% {
        transform: translateY(0px);
    }

    75% {
        transform: translateY(-2px);
    }

}

.cartoon {
    width: 50px;
    height: 50px;
    object-fit: contain;
    object-position: 10px 0;
   margin-top: -6.7px;
    
}

.real {
    width: 50px;
    height: 60px;
   margin-top: -10px; 
    object-fit: cover;
    border-radius: 0 0 100px 100px;
 
}

.profile-img-in{
    transform: translateX( -100vw);
    animation: profile-in 0.2s linear forwards;
}

@keyframes profile-in {
    from {
        
        transform: translateX( -100vw);
    }

    to {
       
        transform: translateX( 0);
    }
}

.profile-img-out {
    opacity: 0;
    transform: translateX( 0);
    animation: profile-out 0.2s 0.2s linear forwards;
}

@keyframes profile-out {
    from {
       
        transform: translateX( 0);
    }

    to {
        
        transform: translateX( -100vw);
    }
}

nav {
    height: 50px;
    max-width: 1300px;
    margin: 0 auto;
    padding: 15px 20px;
    display: flex;
    align-items: center;
    justify-content: space-between;
   
}

.menu-left {
    display: flex;
    align-items: center;
    gap: 15px;
    font-weight: bold;
   
}

.burger {
    cursor: pointer;
    visibility: hidden;
    display: none;
    position: absolute;
    outline: 1px solid transparent;
    outline-offset: 20px;
    border-radius: 6px;
}

.burger:hover {
    transition: 0.4s;
     outline: 1px solid $border;
     outline-offset: 10px;
    border-radius: 6px;
}

.x {
    z-index: 101;
    position: absolute;
    top: 20px;
    right: 20px;
    font-size: 34px !important;
    cursor: pointer;
}

@media screen and (max-width: 1099px) {
    .menu {
     display: none;
     visibility: hidden;
    }

    .burger {
        display:block;
        visibility: visible;
        position: relative;
    }
}