$first: #1E1E1D;
$border: #74C991;

#about {
    min-height: 390px;
    margin-bottom: 20px;
   width: 100%;
}

#about > p::after {
    content: '.';
    height: 16px;
    width: 8px;
    background-color: #9D9C9D;
    color: transparent;
    animation: blinking 1s infinite;
}

#about > h2 span {
    font-size: 1rem;
    font-weight: normal;
}

@keyframes blinking {
    from { background-color: #9D9C9D }
      to { background-color: transparent; }
}

.about {
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 50px;
    position: relative;
}

.yuri-div {
    border-radius: 200px;
    border: 6px solid $border;
  
   width: 300px;
   height: 300px;
    position: relative;
    opacity: 0;
    transform: translateX(-100vw);
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: rgba(255, 255, 255, 0.122);
    box-shadow: inset 0 0 150px $first;
}

.div-appear {
    transition: 300ms ease-out;
    opacity: 1;
    transform: translateX(0);
    
}

.yuri-cartoon {
    width: 270px;
    
    position: absolute;
    top: -60px;
    left: 0;
    transform: translateX(200vw);
   
   
    border-radius: 50px;
}

.profile-appear {
    transform: translateX(0);
    transition: 300ms ease-out;
}

.yuri-photo {
  
   width: 300px;
   
    object-fit: contain;
    object-position: 0 10px;
   border-radius: 200px;
    position: absolute;
    top: -120px;
    left: 0px;
    
}

.set {
    transform: translateX(0);
}

.grow {
    opacity: 0;
    transform: translateX(0);
    animation: grow 0.2s 0.2s forwards linear;
}

@keyframes grow {
    0% {
    opacity: 0;
    width: 50px;
   
    transform: translateX(-100vw);
    }

    40% {
        width: 50px;
       
        opacity: 0;
    }

    100% {
    opacity: 1;
    transform: translateX(0);
    }
}

.shrink {
   
    transform: translateX(0);
    animation: shrink 0.2s forwards linear;
}

@keyframes shrink {
    0% {
    opacity: 1;
    transform: translateX(0);
    }

    60% {
        width: 50px;
       
        opacity: 1;
    }

    100% {
    opacity: 0;
    width: 50px;
   
    transform: translateX(-100vw);
    }
}

.speech-bubble {
    width: 150px;
    position: absolute;
top: 0;
left: 0;
}

.click-me {
    z-index: 2;
   opacity: 0;
    position: absolute;
    top: -80px;
    right:  100px;
    animation: click-me 1s infinite, click-on 0.2s linear forwards;
   
}



.click-off {
    animation: click-off 0.3s linear forwards;
}

@keyframes click-on {
    from {
opacity: 0;
    }

    to {
opacity: 1;
    }
}

@keyframes click-off {
    from {
opacity: 1;
    }

    to {
opacity: 0;
    }
}

.click-me > h2 {
    position: absolute;
    top: 19px;
    left: 36px;
    color: $border;
    text-shadow: 2px 2px 0 black;
}

@keyframes click-me {
    0% {

    }

    50% {
        
transform: translateY(-10px);
    }

    100% {
        
    }
}


