
$border: #74C991;

#portfolio {
    width: 100%;
    display: flex;
    align-items: center;
    flex-direction: column;
    justify-content: center;
   gap: 5px;
   margin-bottom: 20px;
   position: relative;
   border-radius: 6px;
   border: 1px solid rgba(255, 255, 255, 0.295);
   padding-bottom: 20px;
}

#portfolio > h3 {
    margin-block: 10px;
}

.port-img-div {
    position: relative;
   width: 100%;

    height: clamp(200px, 40vw, 550px);
    border-bottom: 1px solid rgba(255, 255, 255, 0.295);
    border-radius: 6px 6px 0 0 ;
    overflow: clip;
}

.port-img-bg {
position:absolute;
   border-radius: 6px 6px 0 0 ;
    padding: 0;
   filter:saturate(20%) blur(10px) ;
   opacity:  0.5;
   height: 100vh;
   top: 50%;
   left: 50%;
   transform: translate(-50%, -50%);
}

.port-img {

    z-index: 3;
    border-radius: 6px;
    padding: 0;
    position: absolute;
    width: clamp(200px, 40vw, 550px);
    outline-offset: 20px;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    box-shadow: 5px 5px 6px rgba(0, 0, 0, 0.441);
    transition: opacity 0.4s ease-in-out;
}

.arrow {
   font-size: 4em !important;
   position: absolute;
   z-index: 4;
   top: 50%;
   transform: translateY(-50%);
   outline: 2px solid rgba(255, 255, 255, 0.323);
   outline-offset: -3px;
   border-radius: 100px;
   margin: 0;
   background-color: rgba(255, 255, 255, 0.088);
   filter: drop-shadow(5px 5px 2px rgba(0, 0, 0, 0.886));
   
   cursor: pointer;
}

.arrow:hover {
    transition: outline 0.4s, color 0.4s, background-color 0.4s;
    outline: 2px solid $border;
    color: $border;
    background-color: rgba(255, 255, 255, 0.202);
}

.arrow:active {
    transition: 0.1s ;
    filter: none;
}


.left {
left: 10px;

}

.right {
    right: 10px;
   
}

.made-with {
   border: 1px solid rgba(255, 255, 255, 0.295);
    border-radius: 5px;
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 10px;

}

.port-tools {
    margin-top: 10px;
   display: flex;
  
   

}

.port-tools > img {
    width: clamp(20px, 8vw, 50px);
   
    margin: 0;
}