
$blue: #9CDCFD;
$border: #74C991;

form {
    margin: 0 auto;
    border-radius: 10px;
}

input[type=text] {
    background-color: transparent;
    border: none;
}
span[contenteditable] {
    width: 100%;
    outline: none;
    caret-color: rgba(255, 255, 255, 0.542);
    color: white;

    word-break: break-all;
   
    
}

#message {
    min-height: 200px;
    
}

