    background: #9D9C9D;
  }
  
  span[contenteditable]::selection {

    background: #9D9C9D;
  } */

.input-clicked[contenteditable]::after {
    content: '.';
    height: 16px;
    width: 8px;
    background-color: #9D9C9D;
    color: transparent;
    animation: blinking 1s infinite;

}

@keyframes blinking {
    from { background-color: #9D9C9D }
    to { background-color: transparent; }
}

.input-div {
    display: flex;
    gap: 6px;
    margin-block: 5px;
    border: 1px solid rgba(255, 255, 255, 0.08);
    padding: 5px;
    border-radius: 4px;
}

[contentEditable=true] br {
    display: none;
}

.submit {
    margin-top: 6px;
    cursor: pointer;
    padding: 7px 14px;
    background-color: transparent;
    color: white;
    border: 1px solid $border;
    border-radius: 4px;
    font-size: 1em;
}

.submit:hover {
    transition: 200ms;
    background-color: rgba(255, 255, 255, 0.135);
}

.form-submitted {
    border: 1px solid rgba(255, 255, 255, 0.08);
    height: 300px;
    border-radius: 4px;
    padding: 5px 12px;
}

.confirm-labels {
    color: $blue;
}

.form-submitted > p::after {
    content: '.';
    height: 16px;
    width: 8px;
    background-color: #9D9C9D;
    color: transparent;
    animation: blinking 1s infinite;
}

@keyframes blinking {
    from { background-color: #9D9C9D }
      to { background-color: transparent; }
}

.email-error {
    margin-block: 0;
    padding-block: 0;
    color: red;
}