$first: #1E1E1D;
$second: white;
$third: #868686;
$shadow: rgba(0, 0, 0, 0.111);
$blue: #9CDCFD;
$border: #74C991;

h1 {
    text-transform: uppercase;
    text-align: center;
    font-size: clamp(40px, 8vw, 100px);
    
    margin-bottom: 20px;
    animation: opacity 1s ease-in;
}

.hero {
    
    position: relative;
    
    height: 100vh;
    background-size: cover;
    
    overflow-x: clip;
}

.hero-container {
  overflow: hidden;
    position: fixed;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}

.hero-title {
    
    position: absolute;
    display: flex;
    flex-direction: column;
    align-items: center;
    z-index: 1;
    top: 50%;

    transform: translateY(-50%);
    transition: opacity 0.5s;
   animation: fade-in 0.5s linear
}

.gone {

animation:gone linear forwards 0.5s;
}

@keyframes gone {
    from {
opacity: 1;
visibility: visible;
    }
    to {
opacity: 0;
visibility: hidden;
    }
    
}

.appear {

animation:appear linear forwards 0.5s;
}

@keyframes appear {
    from {
opacity: 0;
visibility: hidden;
    }
    to {
opacity: 1;
visibility: visible;
    }
    
}

@keyframes fade-in {
    from {
        opacity: 0;
    }
    
    to {
        opacity: 1;
    }
}

.hero-title > h2 {
    position: relative;
}

.hero-title > h2::after {
content: '';
position: absolute;
bottom: -3px;
background-color: $border;
height: 2px;
animation:forwards underline 0.8s 530ms linear;
}

@keyframes underline {
    from {
        left: 50%;
        width: 0;
       
    }

    to {
        left: 0;
        width: 100%;
       
    }
}

#map {
height: 100vh;
width: 100vw;

border: none;
outline: none;
}

.maplibregl-canvas {
    position: absolute;
}

.flag-position {

    width: 0px;

}

.country-modal {
   
    opacity: 0;
   
    position: absolute;
    text-align: center;
    width: 150px;
    padding-block: 10px;
    top: -50px;
    left: -37px;
    background-color: $first;
    border-radius: 6px;
    border: 1px solid $border;
    animation: modal-opacity 0.5s linear forwards;
}

@keyframes modal-opacity {
    from {
        opacity: 0;
    }

    to {
        opacity: 0.5;
    }
}

.country-click {

    opacity: 0;
visibility: hidden;
}

.flag {

    cursor: pointer;
    top:-43px;
    left: -35px;
  position: absolute;
    opacity: 0;
    background-color: white;
    border-radius: 8px;
    color: #FFFFFF;
    font-size: 14px;
    padding: 8px 12px;
    width: 50px;
    display: flex;
    flex-direction: column;
   
}

.drop-flag {
    animation: drop 0.3s linear forwards, float 2s 0.3s ease-in-out infinite;;
}

@keyframes drop {
    0% {
      transform: translateY(-208px) scaleY(0.9);
      opacity: 0;
    }
    5% {
      opacity: 0.7;
    }
    50% {
      transform: translateY(-8px) scaleY(1);
      opacity: 1;
    }
    65% {
      transform: translateY(-25px) scaleY(0.9);
      opacity: 1;
    }
    75% {
      transform: translateY(-30px) scaleY(0.9);
      opacity: 1;
    }
    100% {
      transform: translateY(-8px) scaleY(1);
      opacity: 1;
    }
  }

.flag > img {
    width: 50px
}

.flag::after {
    content: "";
    position: absolute;
    left: 50%;
    top: 100%;
    transform: translate(-50%, -1px);
    
    border-left: 8px solid transparent;
    border-right: 8px solid transparent;
    border-top: 8px solid white;
  }

  @keyframes float {
	0% {
        opacity: 1;
		box-shadow: 0 5px 15px 0px rgba(0,0,0,0.6);
		transform: translatey(-8px);
	}
	50% {
        opacity: 1;
		box-shadow: 0 25px 15px 0px rgba(0,0,0,0.3);
		transform: translatey(-18px);
	}
	100% {
        opacity: 1;
		box-shadow: 0 5px 15px 0px rgba(0,0,0,0.6);
		transform: translatey(-8px);
	}
}

.cloud {
    position: absolute;
    height: 200px;
    filter: saturate(0%);
    opacity: 0;
    transition: opacity 0.3s;
   

    pointer-events: none;
}

.cloud1 {
    
    opacity: 0.5;
    animation: cloud1 55s -30s infinite linear;
}

@keyframes cloud1 {
    from {
        transform: translate(100vw,-50px);
    } to {
        transform: translate(-100vw,-50px);
    }
}

.cloud2 {

    opacity: 0.5;
    animation: cloud2 55s -20s infinite linear;
}

@keyframes cloud2 {
    from {
        transform: translate(100vw,400px);
    } to {
        transform: translate(-100vw,400px);
    }
}

.cloud3 {
    opacity: 0.4;
    animation: cloud3 55s -10s infinite linear;
}

@keyframes cloud3 {
    from {
        transform: translate(100vw,-450px);
    } to {
        transform: translate(-100vw,-450px);
    }
}

.cloud4 {
    transform: translate(100vw,-250px);
    opacity: 0.7;
    animation: cloud4 55s 2s infinite linear;
}

@keyframes cloud4 {
    from {
        transform: translate(100vw,-250px);
    } to {
        transform: translate(-100vw,-250px);
    }
}

.cloud5 {
    height: 300px;
    opacity: 0.7;
    transform: translate(100vw,200px);
    animation: cloud5 55s 11s infinite linear;
    z-index: 3;
}

@keyframes cloud5 {
    from {
        transform: translate(100vw,200px);
    } to {
        transform: translate(-100vw,200px);
    }
}
